<template>
    <div
    class="showcaseCard invert-text"
    :style="styleObject"
    v-drag="translucentColor"
    v-follow
    @up="upHandler"
    @down="downHandler"
    @drop="dropHandler">
      <slot v-if="!up"></slot>
      <div v-if="!up" :style="detailStyleObject" class="details default-background normal-text">
        <slot name="description"></slot>
        <div class="links" @mousedown.stop>
          <a :href="href" v-for="(href, name) in links">{{name}}</a>
        </div>
        <transition name="fade">
          <div class="dropMessage" v-if="dragging">
            Drop it here!
          </div>
        </transition>
      </div>
    </div>
</template>

<script>
export default {
  name: 'showcase-card',
  props: ['color', 'links', 'dragging'],
  data () {
    return {
      up: false,
      droppedColor: ''
    }
  },
  methods: {
    upHandler (e) {
      this.up = true
      this.$emit('up', e)
    },
    downHandler (e) {
      this.up = false
      this.$emit('down', e)
    },
    dropHandler (e) {
      this.droppedColor = e.detail.value
    }
  },
  computed: {
    styleObject () {
      let baseObject = { background: this.color }
      if (!this.up) return baseObject
      return {
        'border-radius': '5em',
        'z-index': '5',
        opacity: 0.2,
        ...baseObject
      }
    },
    detailStyleObject () {
      if (this.droppedColor) {
        return {
          background: this.droppedColor
        }
      }
    },
    translucentColor () {
      return this.color.replace(/,[^,]*\)$/, ',0.6)')
    }
  }
}
</script>

<style scoped>
.showcaseCard {
  height: 10em;
  margin: 0.5em;
  flex-grow: 1;
  flex-basis: 8em;
  position: relative;
  padding: 0 0.5em;
  transition: border-radius 0.2s, opacity 0.2s;
}
.details {
  text-align: justify;
  padding: 0.5em 0.5em 0;
  position: absolute;
  top: 2em;
  bottom: 0.5em;
  left: 0.5em;
  right: 0.5em;
  font-size: 0.6em;
}
.links {
  font-size: 0.8em;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: calc(100% - 1em);
}
.links>a {
  margin: 0 0.2em;
  color: rgba(0,0,0,0.8);
}
.dropMessage {
  color: rgba(0,0,0,0.4);
  font-weight: 600;
  position: absolute;
  text-align: center;
  bottom: 0.5em;
  left: 0.5em;
  right: 0.5em;
  height: 5em;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .2s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}
</style>



