<template>
  <div class="showcase">
    <showcase-card
    @up="isUp = true" @down="isUp = false" :dragging="isUp"
    color="hsla(160, 60%, 40%, 0.8)"
    :links="{Github: 'https://github.com/Ntropish/vue-dragon', 'npm': 'https://www.npmjs.com/package/vue-dragon'}">
    vue-dragon
      <div slot="description">
        <p>A vue directive for drag and drop. Try it out on these cards!</p>

        <p>This directive makes it easy to send expressions
        from one component to another.</p>
      </div>
    </showcase-card>

    <showcase-card
    @up="isUp = true" @down="isUp = false" :dragging="isUp"
    color="hsla(200, 50%, 20%, 0.8)"
    :links="{Github: 'https://github.com/Ntropish/vue-studio'}">
      vue-studio
      <div slot="description">
        An electon app for building Vue projects. It's still a work in progress
        but feel free to check out the repo.
      </div>
    </showcase-card>

    <showcase-card
    @up="isUp = true" @down="isUp = false" :dragging="isUp"
    color="hsla(0, 60%, 40%, 0.8)"
    :links="{Github: 'https://github.com/Ntropish/onify', npm: 'https://www.npmjs.com/package/onify'}">
      onify
      <div slot="description">
        <p>
          An npm package for making event handling easy.
        </p>
        <p>
          Check out the fancy API that's possible with Proxies!
        </p>
      </div>
    </showcase-card>

  </div>
</template>

<script>
import ShowcaseCard from './ShowcaseCard'
export default {
  name: 'showcase',
  components: {
    ShowcaseCard
  },
  mounted () {
  },
  data () {
    return {
      isUp: false
    }
  }
}
</script>

<style scoped>
.showcase {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1em 0;
  overflow: hidden;
  border-radius: 0.5em;
}
.showcase>div {
  height: 10em;
  margin: 0.4em;
  flex-grow: 1;
  flex-basis: 8em;
  position: relative;
  padding: 0 0.5em;
}
.projectDetails {
  text-align: justify;
  padding: 0.2em 0.2em 0;
  position: absolute;
  top: 2em;
  bottom: 0.5em;
  left: 0.5em;
  right: 0.5em;
  font-size: 0.5em;
}
.links {
  font-size: 0.8em;
  position: absolute;
  bottom: 0;
  text-align: center;
  width: calc(100% - 1em);
}
.links>a {
  color: rgba(0,0,0,0.8);
}
p {
  margin-top: 0;
}
</style>



